@import '../../default_variables.scss';

@mixin breakpoint($point) {
  @if $point ==md {

    @media (min-width: 900px) {
      @content;
    }
  }
}

.header {
  &__nav {
    top: 0;
    right: 100%;
    bottom: 0;
    width: 100%;
    height: 100vh;
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: #c6a57a;
    backdrop-filter: blur(2px);
    transform: translate(0);
    transition: 0.3s ease transform;
    font-family: $font_montserrat;

    @include breakpoint(md) {
      margin-left: 1rem;
      transform: none;
      flex-direction: row;
      background: transparent;
      width: auto;
      height: 100%;
      position: static;
    }

    ul {
      list-style: none;
      padding: 0;
      display: flex;
      flex-direction: column;

      @include breakpoint(md) {
        width: 100%;
        flex-direction: row;
        align-items: center;
        margin-bottom: 0;
      }

      li {
        position: relative;

        .cart__length {
          font-size: .7rem;
          position: absolute;
          top: -50%;
          right: 10%;
          height: fit-content;
          color: rgb(255, 255, 255);
        }

        a {
          text-decoration: none;
          font-size: .8rem;
          text-transform: uppercase;
          color: #fff;
          padding: 1rem;
          border-radius: .6rem;
          transition: 0.3s ease all;

          &:hover {
            background: rgba(#fff, 0.1);
          }

          &:active {
            border-radius: calc(#{.6rem} + 6px);
          }
        }

        &:not(:last-child) {
          padding-bottom: 2rem;

          @include breakpoint(md) {
            height: 100%;
            padding-bottom: 0;
            margin-bottom: 0;

          }
        }
      }
    }

    .btn {
      cursor: pointer;
      outline: none;
      padding: 0.75rem 1.25rem;
      border-radius: 12px;
      font-size: 1rem;
      font-family: $font_montserrat;
      background: rgb(51, 51, 51);
      color: #fff;
      border: 1px solid transparent;
      transition: 0.3s ease all;
      margin: 0.50em 0.50em;

      &__login {
        background-color: #be945d;
      }
    }

    &.isMenu {
      transform: translate(100%);
    }
  }

  &__toggle {
    cursor: pointer;
    display: flex;
    align-items: center;
    font-size: 2rem;
    transition: 0.3s ease all;
    position: relative;

    svg {
      fill: #fff;
    }

    &:hover {
      color: rgb(162, 162, 246);
    }

    @include breakpoint(md) {
      display: none;
    }
  }
}